import React from 'react';
import { Link } from "react-router-dom";
import { OpenGuid } from "./Guid";
import "./SideNavi.css";

function SideNavi(props) {
    return <>
        <div className="side_navi">
            <p>
                <Link to="/Home"> </Link>
                <br />
                <Link to="/Login"><b> </b></Link>
                <Link to="/Register"><b> </b></Link>
                <br />
                <Link to="#" onClick={OpenGuid()}> </Link>
                <Link to="/Rules"></Link>
            </p>
        </div>
    </>
}

export default SideNavi;